@import '@style/_variables.scss';

.trips_catalog {
  display: flex;
  gap: 20px;
}

.container {
  display: flex;
  gap: 20px;

  overflow: auto;

  padding-right: 5px;

  /* width */
  &::-webkit-scrollbar {
    width: 10px;
  }

  /* Track */
  &::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  /* Handle */
  &::-webkit-scrollbar-thumb {
    background: #888;
  }

  /* Handle on hover */
  &::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
}
